import {PeopleStore} from "./people";
import {RolesStore} from "./roles";

class RootStore {
    people_store: PeopleStore;
    roles_store: RolesStore;

    constructor() {
        this.people_store = new PeopleStore();
        this.roles_store = new RolesStore();
    }
}

export {RootStore}


